export default [
  {
    path: '/company',
    component: resolve => require(['@/components/views/store/company/company.vue'], resolve),
    meta: {
      title: '公司列表'
    }
  },
  {
    path: '/store',
    component: resolve => require(['@/components/views/store/store/store.vue'], resolve),
    meta: {
      title: '门店管理'
    }
  },
  {
    path: '/cards',
    component: resolve => require(['@/components/views/store/cards/cards.vue'], resolve),
    meta: {
      title: '卡种管理'
    }
  }
]



